<template>
  <panel type="map" title="">
    <ve-map :data="chartData" :settings="chartSettings" :extend="chartExtend" height="280px" />
  </panel>
</template>

<script>
import Panel from "@/components/panel";

import "echarts/lib/component/visualMap";
import VeMap from "v-charts/lib/map.common";

export default {
  data() {
    this.chartSettings = {
      labelMap: {
        province: "",
        terminal: "",
        avg: ""
      }
    };

    return {
      avg: 22,
      chartData: {
        columns: ["province", "terminal"],
        rows: [
          { province: "", terminal: 13 },
          { province: "", terminal: 53 },
          { province: "", terminal: 23 },
          { province: "", terminal: 2 }
        ]
      }
    };
  },

  computed: {
    chartExtend() {
      const avg = this.avg;
      return {
        tooltip: {
          formatter: function (params) {
            return `${params.name}<br/>${params.value || 0} <br/> ${avg}`
          }
        },
        legend: {
          show: false
        },
        visualMap: {
          splitNumber: 3,

          pieces: [
            { gt: this.avg, label: "" },
            { lte: this.avg, label: "" },
            { lte: 0, label: "" }
          ],

          color: ["#3e78dc", "#e83325", "#eeeeee"]
        },
        series: {
          type: "map",
          mapType: "china",
          showLegendSymbol: false,
          top: 0,
          left: 0,
          right: 0,
          label: {
            show: false
          },
          emphasis: {
            label: {
              show: false
            }
          }
        }
      };
    }
  },

  components: {
    VeMap,
    Panel
  }
};
</script>